export function toLogin() {
    let accesstoken = uni.getStorageSync('accesstoken');
    let user_id = uni.getStorageSync("user_id");
    let openId = uni.getStorageSync("openId");
    if (!accesstoken || !user_id) {
        uni.navigateTo({
            url: "/apiPam/login/main"
        });
    } else {
        return true;
    }
}

