import { post } from '../server/api.js';

export function checkUpdater(nowId) {
    const data = post('App.Tools.versionApi', {});
    data.then(res => {
        console.log(res);
        if (res) {
            const updateId = res.data.updateId
            const iosLink = res.data.iosLink
            const androidLink = res.data.androidLink
            if (!updateId) {
                console.log('')
            } else if (nowId != updateId) {
                console.log(nowId);
                console.log(updateId);
                console.log('')
                //  
                let downloadLink = ''
                let ready = false
                // 
                if (plus.os.name.toLowerCase() === 'android') {
                    console.log('')
                    if (androidLink && androidLink !== '#') { // #
                        console.log('')
                        // 
                        if (androidLink.match(RegExp(/.wgt/))) {
                            console.log('wgt')
                            downloadLink = androidLink
                            ready = true
                        } else {
                            console.log('.wgt.apk')
                        }
                    } else {
                        console.log('')
                    }
                } else {
                    console.log('')
                    if (iosLink && iosLink !== '#') { // #
                        console.log('')
                        // (A)iosLinkwgt.wgt
                        if (iosLink.match(RegExp(/.wgt/))) {
                            console.log('wgt')
                            downloadLink = iosLink
                            ready = true
                        } else {
                            console.log('.wgt')
                        }
                    } else {
                        console.log('')
                    }
                }
                if (ready) {
                    console.log('')
                    let downloadTask = uni.downloadFile({
                        url: downloadLink,
                        success: (res) => {
                            if (res.statusCode === 200) {
                                // 
                                console.log('')
                                uni.saveFile({
                                    tempFilePath: res.tempFilePath,
                                    success: (res) => {
                                        const packgePath = res.savedFilePath
                                        // 
                                        console.log('')
                                        downloadTask.abort()
                                        downloadTask = null
                                        // const updated = uni.getStorageSync("updated"); // storage
                                        uni.removeStorageSync("updated");
                                        plus.runtime.install(packgePath, { force: true });
                                        uni.setStorage({
                                            key: "updated",
                                            data: {
                                                completed: true,
                                                packgePath: packgePath
                                            },
                                            success: res => {
                                                console.log("");
                                            }
                                        });
                                        uni.showModal({
                                            title: "",
                                            content: "",
                                            showCancel: false,
                                            complete: () => {
                                                plus.runtime.restart();
                                            }
                                        });

                                    }
                                })
                            }
                        }
                    })
                } else {
                    console.log('')
                }
            } else {
                console.log('')
            }
        }
    }
    );


}